import React from "react";
import { Button } from "react-bootstrap";
import { useNavigate } from "react-router";
import { useUserAuth } from "../context/UserAuthContext";

const Home = () => {
  const { logOut, user } = useUserAuth();
  const navigate = useNavigate();
  const handleLogout = async () => {
    try {
      await logOut();
      navigate("/");
    } catch (error) {
      console.log(error.message);
    }
  };
  return (
    <>
      <div style={{backgroundColor:"#11998e",borderRadius:"5px"}} className="p-4 box mt-3 text-center" >
        Welcome user <br />
        { user.email}
      </div>
      <div className="d-grid gap-2">
        <Button style={{backgroundColor:"#FC466B",borderColor:"#FC466B"}} onClick={handleLogout}>
          Log out
        </Button>
      </div>
    </>
  );
};

export default Home;
